<template>
  <div class="productContainer">

    <div class="cart" v-if="showPage === 'cart'">
      <div class="background">
        <div class="close"> <i class="fa fa-times" aria-hidden="true" @click="selectProduct = {}; showPage = 'main'; stepIndex = 1"></i> </div>
        <div class="step">
          <div class="stepItem" :class="{stepItemActive:stepIndex === 1}">
            <div class="icon">1</div>
            <p>確認購物車</p>
          </div>
          <div class="arrow" :class="{arrowActive:stepIndex === 1}"> <i class="fa fa-arrow-right" aria-hidden="true"></i> </div>
          <div class="stepItem" :class="{stepItemActive:stepIndex === 2}"> 
            <div class="icon">2</div>
            <p>付款與運送方式</p>
          </div>
          <div class="arrow" :class="{arrowActive:stepIndex === 2}"> <i class="fa fa-arrow-right" aria-hidden="true"></i> </div>
          <div class="stepItem" :class="{stepItemActive:stepIndex === 3}">
            <div class="icon">3</div>
            <p>完成訂單</p>
          </div>
        </div>

        <div class="stepOne" v-if="carts.length" v-show="stepIndex === 1">
          <table>
            <thead>
              <th> 商品 </th>
              <th width="10%"> 規格 </th>
              <th width="15%"> 單價 </th>
              <th > 數量 </th>
              <th width="15%"> 小計 </th>
            </thead>
            <tbody>
              <tr class="p-1" v-for=" (item, index) in carts " :key="item.id" >
                <td class="picName">
                  <div class="pic" :style="{backgroundImage :`url(${item.imageUrl})`}"></div>
                  <div class="name">{{item.content}} {{item.title}}</div>
                </td>
                <td>
                </td>
                <td class="price">
                  NT$ {{item.price}}  
                </td>
                <td>
                  <div class="qtyBox">
                    <div class="reduce" :class="{qtyDisabled:item.buyQty<1}" @click="updateCartsBuyQty(index, item.buyQty-1)"><i class="fa fa-minus"></i></div>
                    <input type="text" v-model.number="item.buyQty" disabled>
                    <div class="add" :class="{qtyDisabled:item.buyQty>9}" @click="updateCartsBuyQty(index, item.buyQty+1)"><i class="fa fa-plus"></i></div>
