            </tbody>
          </table>

          <div class="discount">
            <p>如果要使用折扣碼，請在此填入(ok)</p>
            <div class="discountBox">
              <input type="text" v-model.trim="discountCode" @keyup.enter="discount">
              <div class="button" @click="discount">使用折扣碼</div>
            </div>
          </div>

          <div class="total">
            <ul>
              <li>
                <div class="before">商品金額總計</div>
                <div class="after">NT$ {{totalPrice}}</div>
              </li>
              <li>
                <div class="before">+ 運費</div>
                <div class="after">NT$ 0</div>
              </li>
              <li>
                <div class="before">- 折扣</div>
                <div class="after">NT$ 0</div>
              </li>
              <li>
                <div class="before">- 折扣碼優惠</div>
                <div class="after">NT$ 0</div>
              </li>
              <li>
                <div class="before">= 金額總計</div>
                <div class="after">NT$ {{totalPrice}}</div>
              </li>
            </ul>
          </div>
          
          <div class="next" @click="stepIndex = 2">下一步</div>
        </div>

        <div class="stepTwo" v-if="carts.length" v-show="stepIndex === 2">
          <div class="title">
            填寫購買人資訊
          </div>
          <form class="info">
            <div class="left">
              <label for="email">購買人Email</label>
              <input type="text" id="email" name="email" v-validate="'required|email'" placeholder="email"
                :class="{inputError:errors.first('email')}"
              >
              <div class="prompt">{{ errors.first('email') }}</div>
              <label for="name">購買人姓名</label>
              <input type="text" id="name" name="姓名" v-validate="'required'" placeholder="姓名">
              <div class="prompt">{{ errors.first('姓名') }}</div>
              <label for="phone">購買人聯絡電話</label>
              <input type="text" id="phone" name="聯絡電話" v-validate="'required'" placeholder="聯絡電話">
              <div class="prompt">{{ errors.first('聯絡電話') }}</div>

              <div class="box">
                <input type="checkbox" id="isSame" v-model="isSame">
                <label for="isSame">購買人同收件人資料</label>
              </div>

              <template v-if="!isSame">
                <label for="rname">收件人姓名</label>
                <input type="text" id="rname" name="收件人姓名" v-validate="'required'" placeholder="姓名">
                <div class="prompt">{{ errors.first('收件人姓名') }}</div>
                <label for="rphone">收件人聯絡電話</label>
                <input type="text" id="rphone" name="收件人聯絡電話" v-validate="'required'" placeholder="聯絡電話">
                <div class="prompt">{{ errors.first('收件人聯絡電話') }}</div>
              </template>
            </div>

            <div class="right">
              <label for="transport">運送方式</label>
              <select id="transport" v-model="transport">
                <option value="0">請選擇配送方式</option>
                <option value="1">一般宅配</option>
              </select>
              <template v-if="transport == 1">
                <label for="raddress">收件地址</label>
                <input type="text" id="raddress" name="收件地址" v-validate="'required'" placeholder="收件地址">
                <div class="prompt">{{ errors.first('收件地址') }}</div>
              </template>
              <label for="feedback">留言給我們</label>
              <textarea name="" id="feedback" cols="30" rows="5" placeholder="留言給我們"></textarea>
            </div>
          </form>

          <div class="total">
            <ul>
              <li>
                <div class="before">商品金額總計</div>
                <div class="after">NT$ {{totalPrice}}</div>
              </li>
              <li>
                <div class="before">+ 運費</div>
                <div class="after">NT$ 0</div>
              </li>
              <li>
                <div class="before">- 折扣</div>
                <div class="after">NT$ 0</div>
              </li>
              <li>
                <div class="before">- 折扣碼優惠</div>
                <div class="after">NT$ 0</div>
              </li>
              <li>
                <div class="before">= 金額總計</div>
                <div class="after">NT$ {{totalPrice}}</div>
              </li>
            </ul>
          </div>
          
          <div class="buttonGroup">
            <div class="button" @click="stepIndex = 1">上一步</div>
            <div class="button" @click="stepIndex = 2">完成訂單</div>
          </div>
        </div>

        <div class="noItem" v-else>
          <p>購物車沒有內容</p>  
          <div class="button" @click="showPage='main'">back</div>
        </div>

        <div class="footer">
          <div class="top"></div>
          <div class="bottom">POWERED AND SECURED BY</div>
        </div>
      </div>
    </div>


    <div class="selectProduct" v-if="showPage === 'selectProduct'">
      <div class="background">
        <div class="close" @click="selectProduct = {} ; showPage = 'main'"> <i class="fa fa-times" aria-hidden="true"></i> </div>
        <div class="picContent">
          <div class="pic">
            <div class="mainPic" :style="{backgroundImage :`url(${products[selectProduct.mainImgIndex].imageUrl})`}">
            </div>
            <div class="allPic">
              <ul>
                <li v-for = " (item, index) in products " :key="item.id" 
                  @click=" selectProduct.mainImgIndex = index"
                  :style="{backgroundImage :`url(${item.imageUrl})`}"
                  :class="{liActive:selectProduct.mainImgIndex === index}"
                >
                </li>
              </ul>
            </div>
          </div>
          <div class="content">
            <div class="title">{{selectProduct.content}}</div>
              <div class="title">{{selectProduct.title}}</div>
              <div class="price">NT$ {{selectProduct.price}}</div>
              <div class="qtyBox">
                <div class="reduce" :class="{qtyDisabled:selectProduct.buyQty<1}" @click="updateProductsBuyQty(selectIndex, selectProduct.buyQty-1)"><i class="fa fa-minus"></i></div>
                <input type="text" v-model.number="selectProduct.buyQty" disabled>
                <div class="add" :class="{qtyDisabled:selectProduct.buyQty>9}" @click="updateProductsBuyQty(selectIndex, selectProduct.buyQty+1)"><i class="fa fa-plus"></i></div>
              </div>
              <div class="goToCart" @click="showPage='cart'">
                查看購物車
              </div>
          </div>
        </div>
        <div class="detail">
          <div class="title">商品詳情</div>
          <p>{{selectProduct.description}}</p>
        </div>
      </div>
    </div>


    <div class="flyImg" :style="`top: ${flyImgTop}px; left: ${flyImgLeft}px`" v-if="pageFilterProduct[flyImgIndex]" v-show="showflyImg">
      <img :src="pageFilterProduct[flyImgIndex].imageUrl" alt="">
    </div>
    <div class="cartIcon" :class="{cartIconScale:cartIconScale}"  v-show="showPage === 'main'" @click="showPage = 'cart'">
      <i class="fa fa-shopping-cart fa-2x" aria-hidden="true"></i>
      <div class="num">
        {{carts.length}}
      </div>
    </div>
    <div class="message" v-if="isShowMessage">
      {{message}}
    </div>


    <div class="main" v-if="showPage === 'main'">
      <div class="categories">
        <ul>
          <li v-for="(item) in categories" :key="item.id" 
              :class="{categoryActive:item.title===category}"
              @click="category=item.title" 
          >
            {{item.title}}
          </li>
        </ul>
      </div>
      <div class="arrangement">
        <div>排列方式</div>
        <div class="icon"
          :class="{iconActive:arrangement===1}"
          @click="arrangement=1"
        >
          <i class="fa fa-th-large" aria-hidden="true"></i>
        </div>
        <div class="icon"
          :class="{iconActive:arrangement===2}"
          @click="arrangement=2"
        >
          <i class="fa fa-th-list" aria-hidden="true"></i>
        </div>
      </div>

      <div class="products" :class="{change:arrangement===2}">
        <ul>
          <li v-for="(item, index) in pageFilterProduct" :key="item.id" >         <!--  -->
            <div class="pic" :style="{backgroundImage :`url(${item.imageUrl})`,}" @click="selectProduct = item ; selectIndex = index; showPage = 'selectProduct'">
              <div class="detailButton">查看詳情</div>
            </div>
            <div class="content">
              <div class="title">{{item.content}}</div>
              <div class="title">{{item.title}}</div>
              <div class="price">NT$ {{item.price}}</div>
              <div class="qtyBox">
                <div class="reduce" :class="{qtyDisabled:item.buyQty<1}" @click="updateProductsBuyQty(index, item.buyQty-1, $event)"><i class="fa fa-minus"></i></div>
                <input type="text" v-model.number="item.buyQty" disabled>
                <div class="add" :class="{qtyDisabled:item.buyQty>9}" @click="updateProductsBuyQty(index, item.buyQty+1, $event)"><i class="fa fa-plus"></i></div>
              </div>
            </div>
          </li>
        </ul>
      </div>


      <div class="pages">
        <ul>
          <li :class="{'pageDisabled':currentPage===1}" @click="pageChange(currentPage-1)">
            Previous
          </li>
    
          <li v-for="page in totalPage" :key="page" 
              :class="{'liActive':currentPage===page}"
              @click="pageChange(page)">
            {{page}}
          </li>
    
          <li :class="{'pageDisabled':currentPage===totalPage}" @click="pageChange(currentPage+1)">
            Next
          </li>
        </ul>
      </div>
    </div>


    <div class="checkBox" v-if="false">
      <div class="check">

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      categories: [],
      products: [],
      category: '',
      arrangement: 1,

      currentPage: 1,
      totalPage: 0,
      pageNum: 5,

      selectProduct: {},
      selectIndex: 0,
      showPage: 'main',

      showflyImg: 0,
      cartIconScale: 0,
      flyImgIndex: 0,
      flyImgTop: 0,
      flyImgLeft: 0,

      carts: [],

      discountCode: '',

      message:'',
      isShowMessage: 0,

      stepIndex: 1,

      isSame: false,
      transport: 0,
    }
  },
  computed:{
    filterProducts() {
      let arr = [];
      if(this.category === '所有分類商品'){
        arr = this.products;
      } else {
        this.products.forEach(p => {
          if(p.category === this.category){
            arr.push(p);
          }
        })
      }
      this.totalPage = Math.ceil(arr.length / this.pageNum);
      this.currentPage = 1;
      return arr;
    },
    pageFilterProduct(){
      let arr = [];
      let start;
      this.filterProducts.forEach((p,i)=> {
        start = (this.currentPage - 1) * this.pageNum;
        if( start <= i &&  i <= start + this.pageNum - 1 ){
          arr.push(p);
        }
      })
      return arr;
    },
    totalPrice(){
      let vm = this;
      let res =  vm.carts.reduce(( accumulator, currentValue)=>{
        return accumulator + currentValue.buyQty * currentValue.price;
      }, 0);
      return res;
    },
  },
  methods: {
    getCategories() {
      const vm = this;
      const url = 'https://project-2613e.firebaseio.com/categories.json';
      this.$http.get(url).then((response) => {
        vm.categories = response.data;
      });
    },
    getProducts() {
      const vm = this;
      const url = 'https://project-2613e.firebaseio.com/products.json';
      this.$http.get(url).then((response) => {
        vm.products = response.data;
        vm.products.forEach((p,i)=>{
          vm.$set(p, 'buyQty', 0);
          vm.$set(p, 'mainImgIndex', i);
        });
        vm.category = '所有分類商品';
        vm.getCarts();
      });
    },
    pageChange(p){
      p = p < 1 ? 1 : (p > this.totalPage ? this.totalPage : p);
      this.currentPage = p;
    },

    getCarts(){
      this.carts = JSON.parse(localStorage.getItem('carts')) || [] ;

      for( let i = 0; i < this.carts.length; i++){
        for ( let j = 0; j < this.products.length; j++){
          if( this.carts[i].id === this.products[j].id){
            this.products[j].buyQty = this.carts[i].buyQty;
          }
        }
      }
    },
    setCarts(){
      localStorage.setItem('carts', JSON.stringify(this.carts));
    },
    
    // carts.buyQty change => update products => set carts
    updateCartsBuyQty(i, qty){
      let vm = this;
      qty = qty < 0 ? 0 : (qty > 10 ? 10 : qty);

      vm.carts[i].buyQty = qty;

      vm.products.forEach((product, index)=>{
        if(product.id === vm.carts[i].id){
          product.buyQty = qty;
        }
      })

      vm.carts.forEach((item, index)=>{
        if(item.buyQty === 0){
          vm.carts.splice(index, 1);
        }
      })
      vm.setCarts();
    },

    // pageFilterProduct.buyQty change => update carts => set carts
    updateProductsBuyQty(i, qty, e){
      let vm = this;
      qty = qty < 0 ? 0 : (qty > 10 ? 10 : qty);

      let isAdd = qty - vm.pageFilterProduct[i].buyQty;
      if(isAdd === 0){
        return;
      }

      // update pageFilterProduct ( bind view )
      vm.$set(vm.pageFilterProduct[i], 'buyQty', qty);

      // update carts
      let k = false;
      let changing = vm.pageFilterProduct[i];
      vm.carts.forEach((item, index)=>{
        if(item.id === changing.id){
          k = true;
          vm.carts[index].buyQty = changing.buyQty;
        }
      })
      if(k===false){
        vm.carts.push(JSON.parse(JSON.stringify(changing)));
      }
      vm.carts.forEach((item, index)=>{
        if(item.buyQty === 0){
          vm.carts.splice(index, 1);
        }
      })

      // set carts
      vm.setCarts();

      // fly
      if(e){
        vm.flyHandler(i,isAdd, e);
      }
    },

    discount(){
      if(this.discountCode === 'ok'){
        this.message = '套用折扣碼成功';
        this.isShowMessage = 1;
        this.closeMessage();
      }
    },

    closeMessage(){
      let vm = this;
      setTimeout(function(){
        vm.isShowMessage = 0;
      },3000)
    },
    
    flyHandler(i,isAdd,{pageX, pageY}){
      // 設定 flyImg 起點 終點 
      let start;
      let end;

      // cartIcon 
      var scrollHeight = window.pageYOffset ||
      document.documentElement.scrollTop ||
      document.body.scrollTop || 0;
      let cartIcon = document.querySelector('.cartIcon');
      let cartIconTop = cartIcon.offsetTop + scrollHeight + 12.5;
      let cartIconLeft = cartIcon.offsetLeft + 12.5;

      if(isAdd > 0){
        start = {x:pageX, y:pageY};
        end = {x:cartIconLeft, y:cartIconTop};
      } else {
        start = {x:cartIconLeft, y:cartIconTop};
        end = {x:pageX, y:pageY};
      }

      // 起點顯示 flyImg
      let flyImg = document.querySelector('.flyImg');
      this.flyImgIndex = i;
      this.flyImgTop = start.y;
      this.flyImgLeft = start.x;
      // flyImg.style.top = start.y + 'px';
      // flyImg.style.left = start.x + 'px';
      this.showflyImg = 1;

      // 設定 intervalTimes
      let intervalTimes = 50;
      let moveX = (end.x - start.x)/intervalTimes;
      let moveY = (end.y - start.y)/intervalTimes;

      // fly
      if(isAdd < 0){
        this.cartIconScaleHandler();
      }
      let vm = this;
      var interval = setInterval(function(){
        intervalTimes-=1;

        start.x += moveX;
        start.y += moveY;
        vm.flyImgTop = start.y;
        vm.flyImgLeft = start.x;

        if(intervalTimes < 1){
          clearInterval(interval);
          vm.showflyImg = 0;
          if(isAdd > 0){
            vm.cartIconScaleHandler();
          }
        }
      }, 10);
    },
    cartIconScaleHandler(){
      let vm = this;
      vm.cartIconScale = 1;
      setTimeout(function(){
        vm.cartIconScale = 0;
      },200);
    },
  },
  created(){
    this.getCategories();
    this.getProducts();
  },
  mounted(){
    // let isCheck = false;
    // let checkBox = document.querySelector('.checkBox');
    // let check = document.querySelector('.checkBox .check');
    
    // checkBox.addEventListener("click",function(){
    //   isCheck = !isCheck;
    //   if(isCheck){
    //     check.classList.add('checkActive');
    //     setTimeout(()=>{
    //       checkBox.style.backgroundColor = 'blue';
    //     }, 300)
    //   } else {
    //     check.classList.remove('checkActive');
    //     checkBox.style.backgroundColor = '#aaa';
    //   }
    // });
  }
}
</script>

